@charset "utf-8";
@import "vars";
@import '../../node_modules/bulma/sass/utilities/_all';

.title{
  color: $tonico;
}

.subtitle {
  padding: 0.5em;
  color: $tonico;
  text-shadow: 1px 1px 1px black;
}

.column.background {
  color: #bfbfbf;
  background-color: rgba($black, 0.3);
  padding: 0.5em;

  &:first-of-type{
    border-bottom-left-radius: 6px;
    border-top-left-radius: 6px;
  }
  &:last-of-type{
    border-bottom-right-radius: 6px;
    border-top-right-radius: 6px;
  }
}

.cool-bg {
  background-image: linear-gradient(-90deg, rgba(255,255,255,0.3) 0%, rgba(0,0,0,0) 70%);
  &:nth-child(even) {
    background-image: linear-gradient(-90deg, rgba(255,255,255,0.25) 0%, rgba(0,0,0,0) 70%);
  }
}

video.background {
  position: fixed;
  z-index: -1;
  filter: blur(10px);
  object-fit: cover;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
}

p {
  text-align: justify;
}

.container {
  filter: drop-shadow(20px 20px 8px #000);
  .section:first-of-type {
    padding-bottom: 0;
    padding-top: 0;
  }
}

.kitron-video {
  width: 100%;

  @include from($tablet){
    margin: 10px;
    max-width: 350px;
    float: right;
  }

  @include until($tablet){
    margin: 0;
  }
}

.people{
  order: -1;
  img {
    max-height: 30vh;
  }
}

.personas {
  display: flex;
  img {
    margin: 0 auto;
  }
}

.logo{
  display: block;
  margin: 0 auto;
  max-height: 120px;
  padding-top: 10px;
}

.section {
  padding: 1rem 1.5rem;
}